import search from "../../components/search"
import pictureSwiper from "../../components/pictureSwiper.vue"
export default {
	data() {
		return {
			
		}
	},
	components:{
		search,
		pictureSwiper
	},
	onShow() {
		
		/* 判断是否登陆 */
		var res=global.isLogin();
		if(!res){
			uni.showModal({
				title:'提醒',
				content:'请登录',
				success() {
					uni.navigateTo({
						url:"../login/login"
					})
				}
			})
		}
	},
	methods: {

	}
}
